import React from 'react';
import styles from './TypeButton.module.scss';
import {Link} from 'react-router-dom';

interface ITypeButtonProps {
  active?: boolean;
  iconActive?: any;
  iconInActive?: any;
  label?: string;
  labelActiveClassName?: string;
  buttonActiveClassName?: string;
  labelInActiveClassName?: string;
  buttonInActiveClassName?: string;
  to?: string;
}

const TypeButton = (props: ITypeButtonProps) => {
  const active = props.active || location.pathname === props.to;
  return (
    <Link to={props.to} style={{margin: 0, padding: 0}}>
      <button
        className={`${styles.TypeButton} ${active ? props.buttonActiveClassName : props.buttonInActiveClassName}`}>
        {active ? props.iconActive : props.iconInActive}
        <span className={active ? props.labelActiveClassName : props.labelInActiveClassName}>
       {props.label}
    </span>
      </button>
    </Link>
  );
}

TypeButton.defaultProps = {};
export default TypeButton;
