import React from 'react';
import HowItWorksPanel, {IHowItWorksPanelProps} from './components/howItWorksPanel/HowItWorksPanel';


interface IHowItWorksPanelsProps {
  panels: IHowItWorksPanelProps[];
}

const HowItWorksPanels = (props: IHowItWorksPanelsProps) => (
  <>
    {props
      .panels
      .map((panel, index) => <HowItWorksPanel key={index} {...panel}/>)}
  </>
);

export default HowItWorksPanels;
