import React from 'react';

import styles from './WhenToBuySquare.module.scss';


interface IWhenToBuySquareProps {
  title: string;
  lines: string[];
}

const WhenToBuySquare = (props: IWhenToBuySquareProps) => (
  <div className={styles.WhenToBuySquare}>
    <div className={styles.imgBox}/>
    <div className={styles.content}>
      <h3>{props.title}</h3>
      {props
        .lines
        .map((line, index) => <div key={index} className={styles.line}>{line}</div>)}
    </div>
  </div>
);

WhenToBuySquare.defaultProps = {
  title: '',
  lines: [],
}

export default WhenToBuySquare;
