import React from 'react';
import styles from './HowItWorksPanel.module.scss';
import InfoCard from 'gui/pages/mainRouter/pages/ad/components/commons/infoCard';
import HowItWorksDemonstration from './components/howItWorksDemonstration';

export interface IHowItWorksPanelProps {
  src?: string;
  titleIcon?: any;
  title?: string;
  lines?: string[];
  children?: any;
}

const HowItWorksPanel = (props: IHowItWorksPanelProps) => (
  <InfoCard title={props.title} className={styles.HowItWorksPanel}
            iconContainerClassName={styles.howItWorksIcon}
            icon={props.titleIcon}
            defaultExpanded={true}
  >
    <div className={styles.content}>
      {props.children}
      {props.lines.map((line, index) => <div key={index} className={styles.line}>
        {line}
      </div>)}
    </div>
    <HowItWorksDemonstration src={props.src}/>
  </InfoCard>
);

HowItWorksPanel.defaultProps = {
  lines: [],
};
export default HowItWorksPanel;
